@import "~@/scss/var.scss";

























































@keyframes rolldown {
    0% {
    visibility: visible;
    transform: rotateX(180deg) perspective(500px);
    }
    70% {
    visibility: visible;
    transform: rotateX(-20deg);
    }
    100% {
    visibility: visible;
    transform: rotateX(0deg);
    }
}

.rolldown{
    visibility: hidden;
    animation: rolldown .7s 1;
    transform-origin: 50% 0;
    animation-fill-mode: forwards;
}
