@import "~@/scss/var.scss";






















































































































































































#article,
section.article {
    display: none;
}
// ==================================================================================
html {        
    // -webkit-overflow-scrolling: touch;

    &::-webkit-scrollbar
    {
        width: 12px;
        height: 8px;
        background-color: #eaeaea;
    }

    &::-webkit-scrollbar-thumb
    {
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #d8d8d8;

        &:hover{
            background-color: #ecebeb;
        }
    }

    &.hide-scrollbar{
        &::-webkit-scrollbar,
        &::-webkit-scrollbar-thumb { 
            width: 0px;
            display: none; 
        }
    }
    
    // &.viewer,
    // &.readme{
    //     @media (min-width: 600px) { 
    //         &::-webkit-scrollbar,
    //         &::-webkit-scrollbar-thumb { 
    //             width: 0px;
    //             display: none; 
    //         }
    //     }
    // }
}
// ==================================================================================
.v-application{
    transition: transform 0.4s;
    &.is-parallax .v-main{
        transform: translate(40px, 10px);
    }
    
    .Sidebar-content.v-navigation-drawer{
        transition-duration: 0.4s;
        transition-property: transform,visibility,width,opacity;
        transition-timing-function: ease;
        &.v-navigation-drawer--close{
            opacity: 0.3;
            transform: translateX(-100%) translateY(-40px) !important;
        }
    }
}
// ==================================================================================
$width-sidebar: 300px;
$width-scrollbar: 100px;
$width-article: calc( 100vw - #{$width-sidebar} - #{$width-scrollbar} );

.hidden-sideblock{
    height: calc( 100vh - 48px );
    // background: brown;
    display: block;
}

.md-viewer .sidebar-bg{
    width: $width-sidebar;
}

.md-viewer .article{
    width: $width-article;
    flex-grow: 1;
}

.md-viewer .scrollbar-bg{
    width: $width-scrollbar;
}

// ==================================================================================
.Vue-Toastification__container{
    padding-top: 68px;

    .toast-notification{
        padding: 8px 16px;
        width: 400px;
    }

    @media only screen and (max-width: 600px){
        .toast-notification{
            border-radius: 15px;
            margin: 0 auto;
            margin-bottom: 8px;
            width: 90%;
            max-width: 400px;
            min-height: 46px;
            padding: 2px 6px;
            padding-left: 12px;
        }

        .Vue-Toastification__toast-body{
            font-size: 14px;
            line-height: 1.5;
        }

        .Vue-Toastification__icon{
            margin-right: 12px;
        }
    }

    .Vue-Toastification__toast--info{
        background-color: #1290de;
        background: linear-gradient(135deg, #0e4597 15%, #1290de 76%, #0e4597 80%);
    }

    .Vue-Toastification__toast--success {
        background-color: #4caf50;
        background: linear-gradient(135deg, #0f6d46 15%, #29c483 76%, #0f6d46 80%);
    }

    .Vue-Toastification__toast--warning{
        background-color: orange;
        background: linear-gradient(135deg, #d76402 15%, #ffa500 76%, #d76402 80%);
    }

    .Vue-Toastification__toast--error {
        background-color: #ff5252;
        background: linear-gradient(135deg, #b63b3b 15%, #ff5252 76%, #b63b3b 80%);
    }

    .Vue-Toastification__toast-body,
    .toast-notification{
        display: flex;
        align-items: center;
    }

    .Vue-Toastification__close-button{
        background: #ffffff60;
        border-radius: 100%;
        height: 24px;
        width: 24px;
        padding-left: 4px;
        padding-right: 4px;
        margin-left: 14px;
        opacity: 1;
        transition-property: visibility, opacity, background;
        transition: visibility 0s, opacity 0.2s linear, background 0.2s linear;

        &:hover{
            background: #ffffff91;
        }
    }
}
