@import "~@/scss/var.scss";












































































































$height-scroll-bar: 4px;
.OverflowElement .overflow-scroller{
    white-space: nowrap;
    height: 100%;
    .scroll-content{
        height: 100%;
        display: flex;
        align-items: center;
    }

    .scrollbar-track.scrollbar-track-x{
        border-radius: 100px;
        height: $height-scroll-bar;
        .scrollbar-thumb{
            height: $height-scroll-bar;
        }
    }
}
.OverflowElement.child-overflow{
    height: calc( 100% + 4px );
    padding-right: 22px;
    position: relative;
    margin-top: calc( #{$height-scroll-bar} * -1 );
    .scroll-content{
        padding-top: $height-scroll-bar;
    }
    .overflow-append{
        position: absolute;
        right: -4px;
        width: 24px;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .overflow-append-btn{
            padding-right: 0px !important;
            padding-left: 0px !important;
            min-width: 24px;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center;
            .v-icon{
                height: 100%;
            }
            &.dots-btn{
                padding-top: 16px;
            }
            &.arrow-btn{
                padding-top: 8px;
            }
        }
    }
}
