@import "~@/scss/var.scss";



















































































































































































.SidebarTOC .v-list-item{
    &.h2{
        padding-left: 25px;
    }

    &.h3,
    &.h4,
    &.h5,
    &.h6{
        padding-left: 45px;
    }
}

.theme--light.v-list-item:hover::before,
.theme--light.v-list-item--active:hover::before, 
.theme--light.v-list-item--active::before {
    border-radius: 15px !important;
}
